"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
require("./performance.scss");
var func_1 = require("./func/func");
var faqPop_1 = require("./class/faqPop");
var StIFrame_1 = require("./class/StIFrame");
var StAjax_1 = require("./class/StAjax");
document.onreadystatechange = function (evt) {
    console.log(document.readyState);
    if (document.readyState === "complete") {
        docReady();
    }
};
function docReady() {
    func_1.setBodyHeight();
    //
    var stFrame = new StIFrame_1.StIFrame();
    stFrame.init();
    stFrame.switchQrcArea();
    //ajaxer
    var ajaxer = new StAjax_1.StAjax({
        url: 'http://dev.akaba.cn/admin/api/getQrcInfo?acid=ADAOHRbx1me2mw&safe=5b85chno3ca6bg5',
        crossDomain: true,
    });
    ajaxer.whenSuccess(function (reply) {
        stFrame.qrc.wechat.link = reply.qrcode;
        stFrame.qrc.wechat.tip = reply.tip;
        stFrame.setQrcBlock('wechat');
    }).send();
    //
    var pop = new faqPop_1.FaqPop();
    // 
    var faqLink = document.body.querySelector('.faq-link');
    faqLink.addEventListener('click', function () {
        pop.show();
    });
    //
    window.addEventListener('resize', function () {
        func_1.setBodyHeight();
    });
    // 
    var switchFuncBtn = document.body.querySelector('.switch-func-btn');
    switchFuncBtn.addEventListener('click', function () {
        stFrame.toggleShowController();
    });
}
